@import url(https://fonts.googleapis.com/css2?family=Sora:wght@300&display=swap);
*,
html {
  box-sizing: border-box;
}
body {
  background-color: rgb(232, 242, 241);
}

.center {
  padding-top: 50px;
}
.loading {
  margin-top: 40vh;
  text-align: center;
}
.title {
  padding: 0;
  margin-bottom: 20px;
  margin-top: 20px;
  font-weight: 800;
}
.line {
  width: 120px;
  background-color: rgba(3, 192, 167, 0.816);
  height: 5px;
  margin: auto;
  margin-top: 0;
  margin-bottom: 30px;
}
.wrapper {
  /* display: flex; */
}
ul li {
  list-style: none;
}
.side-bar {
  padding: 10px;
}
.item {
  width: 100%;
  margin-bottom: 7px;
  text-align: center;
  text-transform: uppercase;
  font-size: 18px;
  color: rgba(3, 192, 167, 0.816);
  font-weight: 500;
  border: 0;
  display: block;
  margin-top: 20px;
  background-color: rgb(232, 242, 241);
  cursor: crosshair;
}
.btn-active {
  border-left: 2px solid rgba(3, 192, 167, 0.816);
}
.name {
  background-color: lightgrey;
  text-transform: uppercase;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 5px;
}
.group {
  display: flex;
}
.group .icon {
  color: rgba(3, 192, 167, 0.816);
  margin-right: 20px;

  align-self: center;
}
.info {
}
.button {
  margin-top: 20px;
  margin: auto;
  text-align: center;
}
.button .btn {
  background-color: rgba(3, 192, 167, 0.816);
}
.click {
  border: none;
  color: rgba(3, 192, 167, 0.816);
  font-weight: 700;
  font-style: oblique;
}
@media only screen and (max-width: 559px) {
  .side-bar {
    /* padding: 10px; */
    display: flex;
    margin-bottom: 20px;
  }
  .item {
    width: 100%;
    /* margin-bottom: 7px; */
    text-align: center;
    text-transform: uppercase;
    font-size: 18px;
    color: rgba(3, 192, 167, 0.816);
    font-weight: 500;
    border: 0;
    /* margin-top: 20px; */
    background-color: rgb(232, 242, 241);
    cursor: crosshair;
  }
  .btn-active {
    border-bottom: 2px solid rgba(3, 192, 167, 0.816);
  }
}

